  font-display: swap;
  src: url("../fonts/mon/400.woff") format("woff");
}
/* Montserrat medium (normal) */
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/mon/500.woff") format("woff");
}
