html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
}
*::before *::after {
  margin: inherit;
  padding: inherit;
}

body {
  box-sizing: inherit;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  min-height: 100vh;
  min-width: 360px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}

picture {
  display: flex;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.map__wrapper {
  padding: 20px;
}

.map__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.map__link {
  color: #000000;
  font-size: 16px;
  line-height: 1.3;
  text-decoration: none;
}

@media (min-width: 480px) {
  .map__wrapper {
    padding: 30px;
  }
  .map__link {
    font-size: 22px;
    line-height: 1.4;
  }
}
@media (min-width: 560px) {
  .map__wrapper {
    padding: 40px;
  }
  .map__link {
    font-size: 22px;
  }
}
@media (min-width: 640px) {
  .map__wrapper {
    padding: 40px;
  }
  .map__link {
    font-size: 24px;
    line-height: 1.5;
  }
}
@media (min-width: 760px) {
  .map__wrapper {
    padding: 40px;
  }
  .map__link {
    font-size: 26px;
    line-height: 1.6;
  }
}
@media (min-width: 1024px) {
  .map__wrapper {
    padding: 50px;
  }
  .map__link {
    font-size: 28px;
  }
}
.container {
  max-width: 1149px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
  .container .breadcrumbs {
    display: none;
  }
}
.container .breadcrumbs {
  grid-column: 1/-1;
  margin-top: 20px;
  margin-bottom: 36px;
}
.container .breadcrumbs .breadcrumbs__item {
  font-size: 13px;
  color: #373C41;
}
.container .breadcrumbs .link {
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #373C41;
}

.button {
  color: inherit;
  font-family: "Old Standard TT", serif;
  font-size: 18px;
  line-height: inherit;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  cursor: pointer;
  user-select: none;
  border: 1px solid black;
  outline: none;
  background-color: transparent;
}
.button--disabled, .button:disabled {
  cursor: default;
  pointer-events: none;
  opacity: 0.3;
}
@media (hover: hover) and (pointer: fine) {
  .button:hover {
    color: crimson;
    border-color: crimson;
  }
  .button:hover svg {
    color: currentColor;
  }
}
.button:focus {
  color: crimson;
  border-color: crimson;
}
.button:focus svg {
  color: currentColor;
}
.button:active {
  color: crimson;
  border-color: crimson;
}
.button:active svg {
  color: currentColor;
}

.button__icon--left {
  margin-right: 8px;
}
.button__icon--right {
  margin-left: 8px;
}

.link {
  color: inherit;
  font-family: "Old Standard TT", serif;
  display: inline-flex;
  align-items: center;
  user-select: none;
  outline: none;
  border: none;
  background-color: transparent;
}
.link--disabled, .link:disabled {
  cursor: default;
  pointer-events: none;
  opacity: 0.3;
}
.link--underlined {
  text-decoration: underline;
}
@media (hover: hover) and (pointer: fine) {
  .link--decorate:hover {
    border-color: crimson;
  }
}
.link--decorate:focus {
  border-color: crimson;
}
.link--decorate:active {
  border-color: crimson;
}
.link--decorate-underlined {
  border-bottom: 1px solid black;
}
.link--decorate-dotted {
  border-bottom: 1px dotted black;
}
@media (hover: hover) and (pointer: fine) {
  .link:hover {
    color: crimson;
  }
  .link:hover svg {
    color: currentColor;
  }
}
.link:focus {
  color: crimson;
}
.link:focus svg {
  color: currentColor;
}
.link:active {
  color: crimson;
}
.link:active svg {
  color: currentColor;
}

.link__icon--left {
  margin-right: 8px;
}
.link__icon--right {
  margin-left: 8px;
}

.pagination {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 960px;
}

.pagination__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  order: 2;
}

.pagination__item {
  margin-right: 8px;
}
.pagination__item:last-child {
  margin-right: 0;
}
.pagination__item--empty a {
  pointer-events: none;
}

.pagination__link,
.pagination__button {
  color: inherit;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  border: 1px solid transparent;
  border-radius: 4px;
  transition: border 0.3s ease, background-color 0.3s ease, color 0.3s ease;
  outline: none;
  user-select: none;
}

.pagination__link {
  color: #000000;
  width: 40px;
}
@media (hover: hover) and (pointer: fine) {
  .pagination__link:hover {
    color: #000000;
    font-weight: 700;
    border-color: #000000;
  }
}
.pagination__link:focus {
  color: #000000;
  font-weight: 700;
  border-color: #000000;
}
.pagination__link:active {
  color: #000000;
  font-weight: 700;
  border-color: #000000;
}

.pagination__button {
  padding: 0 10px;
}
.pagination__button--previous {
  order: 1;
  margin-right: 8px;
  border-color: #000000;
}
@media (hover: hover) and (pointer: fine) {
  .pagination__button--previous:hover {
    color: #999999;
    border-color: #999999;
  }
}
.pagination__button--previous:focus {
  color: #999999;
  border-color: #999999;
}
.pagination__button--previous:active {
  color: #999999;
  border-color: #999999;
}
.pagination__button--next {
  order: 3;
  color: #ffffff;
  background-color: #000000;
  margin-left: 8px;
}
@media (hover: hover) and (pointer: fine) {
  .pagination__button--next:hover {
    color: #b3b3b3;
    background-color: #1a1a1a;
  }
}
.pagination__button--next:focus {
  color: #b3b3b3;
  background-color: #1a1a1a;
}
.pagination__button--next:active {
  color: #b3b3b3;
  background-color: #1a1a1a;
}

.breadcrumbs__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

.breadcrumbs__item {
  display: flex;
  align-items: center;
  margin-right: 10px;
}
.breadcrumbs__item:last-child {
  margin-right: 0;
}

.breadcrumbs__link {
  margin-right: 10px;
}
.breadcrumbs__link--current {
  color: rgba(0, 0, 0, 0.3);
  pointer-events: none;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.custom-select {
  width: max-content;
  min-height: 40px;
  user-select: none;
  position: relative;
}
.custom-select select {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.custom-select.is-active .custom-select__options {
  opacity: 1;
  display: block;
}

.custom-select__styled {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffffff;
  background-color: #e74c3c;
  cursor: pointer;
  transition: background-color 0.3s ease;
  outline: none;
  padding: 10px 15px;
}
.custom-select__styled svg {
  margin-left: 12px;
}
.custom-select__styled:hover, .custom-select__styled:focus {
  background-color: #ab3326;
}
.custom-select__styled.is-active svg {
  transform: rotate(-90deg);
  transition: transform 0.3s ease;
}

.custom-select__options {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  display: none;
  animation: fade-in 0.6s;
  transition: all 0.3s ease;
}

.custom-select__option {
  padding: 10px 15px;
  color: #ffffff;
  background-color: #e74c3c;
  transition: background-color 0.3s ease;
  cursor: pointer;
  outline: none;
}
.custom-select__option:hover, .custom-select__option:focus {
  background-color: #ab3326;
}

.custom-select__option[rel=hide] {
  display: none;
}

.custom-input {
  display: flex;
  flex-direction: column;
}
.custom-input.is-invalid .custom-input__field {
  border-color: #ff553e;
}
.custom-input.disabled {
  opacity: 0.5;
}
.custom-input.disabled .custom-input__label {
  pointer-events: none;
}
.custom-input.disabled .custom-input__field {
  pointer-events: none;
}

.custom-input__label {
  color: #7c96b0;
  margin-bottom: 8px;
}

.custom-input__field {
  font-family: inherit;
  color: #BDBDBD;
  font-size: 13px;
  line-height: 15px;
  padding: 20px 15px;
  outline: none;
  border: 2px solid #E7E7E7;
  border-radius: 3px;
}
.custom-input__field::placeholder {
  color: #7c96b0;
  color: #BDBDBD;
}
@media (hover: hover) and (pointer: fine) {
  .custom-input__field:hover {
    color: #1f1f1f;
    border: 2px solid #2d8df1;
  }
  .custom-input__field:hover::placeholder {
    color: #1f1f1f;
  }
}
.custom-input__field:focus {
  color: #1f1f1f;
  border: 2px solid #2d8df1;
}
.custom-input__field:focus::placeholder {
  color: #1f1f1f;
}
.custom-input__field:active {
  color: #1f1f1f;
  border: 2px solid #2d8df1;
}
.custom-input__field:active::placeholder {
  color: #1f1f1f;
}

.tags__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  margin-left: -8px;
  margin-top: -8px;
}

.tags__item {
  margin-left: 8px;
  margin-top: 8px;
}

.tags__link {
  color: #001a34;
