}
@media (hover: hover) and (pointer: fine) {
  .tags__link:hover {
    color: #003ead;
    background-color: #e2e7ec;
  }
}
.tags__link:focus {
  color: #003ead;
  background-color: #e2e7ec;
}
.tags__link:active {
  color: #003ead;
  background-color: #e2e7ec;
}

.promo {
  background-color: #A7E600;
  background-image: url("../img/desktop/promo-mask.jpg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
