    background-image: none;
    position: relative;
  }
  .promo--secondary::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: -1;
    width: 100%;
    height: 200px;
    background-color: #A7E600;
