  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 20px;
}
@media (max-width: 767px) {
  .gallery__list--main {
    grid-template-columns: auto;
  }
  .gallery__list--main .gallery__item {
    grid-column: span 1;
  }
  .gallery__list--main .gallery__item--small picture {
    margin: 0;
  }
  .gallery__list--main .gallery__item--small a {
    display: grid;
    grid-template-rows: repeat(2, auto);
    position: relative;
  }
  .gallery__list--main .gallery__item--small a::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(55, 60, 65, 0.5);
  }
