  }
}
.gallery__list--projects {
  gap: 10px;
}
@media (max-width: 1023px) {
  .gallery__list--projects {
    grid-template-columns: repeat(2, 1fr);
  }
  .gallery__list--projects .gallery__item {
    grid-column: span 1;
  }
  .gallery__list--projects .gallery__item--small picture {
    margin: 0;
  }
  .gallery__list--projects .gallery__item--video {
    grid-column: span 2;
